.quickbar {
  margin-bottom: $spacer;
  background: darken($body-bg, 4%);
  padding: $spacer / 3;
  border-radius: 0.25rem;
  display: flex;
  justify-content: space-evenly;
  align-content: space-evenly;
  align-items: center;
  @include media-breakpoint-down(sm) {
    flex-wrap: wrap;
  }

  &-wrap {
    flex-wrap: wrap;
  }

  &-item {
    min-width: 140px;
    flex: 1 1 0px;
    text-align: center;
    padding: $spacer / 3;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    @include media-breakpoint-down(sm) {
      width: 50%;
    }
  }

  &-link {
    color: $body-color !important;
    background: white;
    border-radius: 0.25rem;
    border: 1px solid transparent;
    width: 100%;
    padding: 1rem 0;
    font-weight: 500;
    transition: transform 0.15s ease-out, box-shadow 0.15s ease-out;

    figure {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-bottom: 0.25rem;

      fa-icon {
        font-size: 1.5rem;
        display: block;
        color: theme-color('primary');
      }
    }

    &:hover {
      transform: translateY(-2px);
      box-shadow: 0 0.5rem 1.25rem rgba(0, 0, 0, 0.125);
      text-decoration: none;
    }

    &:active {
      transform: translateY(-1px);
      background: mix(white, $body-bg, 75%);
      box-shadow: 0 0.25rem 0.75rem rgba(0, 0, 0, 0.1);
    }
  }

  &.sgpmx {
    .quickbar-link {
      display: flex;
      flex-direction: column;
      align-items: center;

      figure {
        margin-bottom: 0.35rem;
        background-color: rgba(theme-color('warning'), 0.16);
        border-radius: 50%;
        width: 38px;
        height: 38px;

        &.success {
          background-color: rgba(theme-color('success'), 0.15);
        }

        fa-icon {
          width: 34px;
          height: 34px;
        }
      }
    }
  }
}

.statistic-box {
  margin: 0.5rem 0;
  .box-content {
    position: relative;
    margin: 0 auto;
    width: 190px;
    height: 190px;

    .box-chart-wrapper {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -95px;
      margin-left: -56%;
      width: 112%;
      height: 190px;
    }
  }
}

.activity-feed {
  padding-left: 5px;

  .feed-item {
    position: relative;
    padding-bottom: 1rem;
    padding-left: 1.5rem;
    border-left: 2px solid #e4e8eb;

    &:last-child {
      border-color: transparent;
      padding-bottom: 0;
    }

    &:after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: -6px;
      width: 10px;
      height: 10px;
      border-radius: 6px;
      background: #fff;
    }

    &.success:after {
      border: 2px solid theme-color('success');
    }

    &.info:after {
      border: 2px solid theme-color('info');
    }

    &.danger:after {
      border: 2px solid theme-color('danger');
    }

    .date {
      position: relative;
      top: -5px;
      color: $gray-400;
      font-size: 80%;
    }

    .text {
      position: relative;
      top: -3px;
      font-size: 0.875rem;
    }
  }
}

.profile-completeness {
  position: relative;
  display: flex;
  justify-content: center;
  margin-bottom: 0.5rem;

  user-avatar {
    position: absolute;
    display: block;
    width: 96px;
    height: 96px;
    top: 16px;
  }
}

.progress-circle {
  svg {
    transform: rotate(-90deg);
  }

  &-meter,
  &-value {
    fill: none;
  }

  &-meter {
    stroke: $gray-100;
  }

  &-value {
    stroke: $gray-200;
    transition: all 500ms ease-out;
  }
}

.profile-value {
  font-size: 0.9375rem;
}

.card-preview {
  display: block;
  border: 1px solid $border-color;
  border-radius: 0.5rem;
  background: $gray-100;
  margin-bottom: 1rem;
  overflow: hidden;
  padding: 0.5rem 0;
  color: $body-color;

  &-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 0.5rem 0.5rem;
  }

  &:last-child {
    margin-bottom: 0;
  }

  .table {
    td {
      &:first-child {
        padding-left: 0.8rem !important;
      }
      &:last-child {
        padding-right: 0.8rem !important;
      }
    }
    tbody tr:nth-of-type(odd) {
      background-color: #fdfdfd;
    }
  }

  &:hover {
    border-color: mix(theme-color('primary'), $border-color);
    cursor: pointer;
    text-decoration: none;
    color: $body-color;
  }
}

@media all and (max-width: 767px) {
  .account-balance-dropdown {
    .dropdown-menu.show {
      left: -200px !important;
      transform: none !important;
    }
  }
}

.badge-profile {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  bottom: 20%;
  right: 12%;
  width: 24px;
  height: 24px;
  border-radius: 12px;
  border: 2px solid white;

  svg {
    top: 2px;
    position: relative;
  }
}
