@each $color, $value in $theme-colors {
  .modal-#{$color} {
    .modal-header {
      background-color: $value;
      color: #fff !important;
      border-bottom: none;

      .modal-title {
        color: #fff;
      }

      a {
        color: $white;

        &:hover {
          color: darken($white, 10%);
        }
      }
    }

    .close-modal {
      color: rgba($white, 0.9);

      &:focus,
      &:hover {
        color: $white;
      }
    }
  }
}

.modal-header {
  padding: 1rem $modal-inner-padding;
  border-bottom: 1px solid $border-color;
  align-items: center;
}

.modal-title {
  font-weight: 400;
}

.close-modal {
  font-weight: 400;
  font-size: 1.75rem;
  line-height: 0.5;
}

.modal-subheader {
  display: flex;
  align-content: center;
  align-items: center;
  padding: ($modal-inner-padding / 2) $modal-inner-padding;
  background-color: $gray-800;
  color: $white;
}

.modal-body {
  &.scrollable {
    max-height: 450px;
    overflow: auto;
  }

  label {
    font-size: $font-size-base;
  }
}

.modal-footer {
  background: $modal-footer-bg;
  border-top: 1px solid $border-color;
  padding: 1rem $modal-inner-padding;
}

.modal-xl {
  max-width: calc(100vw - 50px);
}

.modal-primary.security-check-modal {
  z-index: 99999;
}
