label {
  color: $gray-600;
  font-size: 90%;
}

.custom-control-label {
  font-size: 100%;
  color: $body-color;
}

// form validation

.form-validation {
  &.ng-submitted .ng-invalid:not(ng-form),
  &.ng-submitted ng-select.ng-invalid > div,
  .ng-invalid.ng-dirty:not(ng-form),
  .ng-submitted .ng-invalid,
  .ng-submitted ng-select.ng-invalid > div,
  ng-select.ng-invalid.ng-dirty .ng-select-container {
    border-color: theme-color('danger') !important;
  }
}

.form-control-feedback {
  margin-top: 0.25rem;

  &:not(:first-child) {
    display: none;
  }
}

.error-messages,
ng-message {
  display: block;
  color: theme-color('danger');
}

.info-messages {
  display: none;
  color: theme-color('info');
}

input:required,
select:required {
  box-shadow: none;
}

// form control
.form-control {
  &::-webkit-input-placeholder {
    color: $input-placeholder-color;
  }
}

.has-info {
  .form-control {
    border-color: theme-color('info') !important;
  }

  .form-control-label {
    color: theme-color('info') !important;
  }

  .info-messages {
    display: block;
  }
}

.custom-control.custom-checkbox {
  margin-top: 0.4rem;
  margin-bottom: 0.3em;

  & > .custom-control-label {
    &::after,
    &::before {
      top: 0.1rem;
    }
  }
}

.form-block {
  margin-bottom: $spacer;
  padding-left: 1.25rem;
  padding-right: 1.25rem;
  @include media-breakpoint-down(sm) {
    padding-left: 0;
    padding-right: 0;
  }
}

ng-select.border-info .ng-select-container {
  border-color: theme-color('info') !important;
}

ng-select.border-danger .ng-select-container {
  border-color: theme-color('danger') !important;
}

.input-large {
  flex-grow: 1;
}
