.login-page {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 15px;
}

.login-logo {
  display: block;
  width: $login-logo-width;
  margin: $spacer auto;
}

.alert-login {
  width: 100%;
  max-width: 430px;
  margin: 0 auto 1rem;
  padding-right: 45px;
}

.login-box {
  width: 100%;
  max-width: 440px;
  margin: 0 auto 0.75rem;
}
