// Bootstrap overrides

//
// Color system
//

$white: #fff;
$gray-100: #f8f9fb; // gray-lightest
$gray-200: #e9ecef; // gray-lighter
$gray-300: #bcc4d4; // gray-light
$gray-400: #a6aebe; // gray
$gray-500: #839ba8;
$gray-600: #7f8fa4;
$gray-700: #5a6269;
$gray-800: #1f2e3d; // mix(#12263f, #262f3d);
$gray-900: #060707;
$black: #000;

$blue: #3983ec;
$indigo: #6610f2;
$purple: #6f42c1;
$pink: #e83e8c;
$red: #d94a3d;
$orange: #ffa523;
$yellow: #ffc107;
$green: #50b652;
$teal: #42ba96;
$cyan: #1f9bcf;

$colors: (
  'blue': $blue,
  'indigo': $indigo,
  'purple': $purple,
  'pink': $pink,
  'red': $red,
  'orange': $orange,
  'yellow': $yellow,
  'green': $green,
  'teal': $teal,
  'cyan': $cyan,
  'white': $white,
);

$theme-colors: (
  primary: $blue,
  secondary: $teal,
  success: $teal,
  info: $blue,
  warning: $orange,
  danger: $red,
  light: $gray-100,
  'gray': $gray-600,
  dark: $gray-800,
);

// Grid containers
$container-max-widths: (
  sm: 1196px,
  md: 1197px,
  lg: 1198px,
  xl: 1199px,
);
$grid-columns: 24;

// Options
//
// Quickly modify global styling by enabling or disabling optional features.

$enable-transitions: true;
$enable-rounded: true;

// Body
//
// Settings for the `<body>` element.

$body-bg: #f0f3f7;
$body-color: #495057;

$font-family-sans-serif: 'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial,
  sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
$font-weight-bold: 500;

$border-color: #e6eaf0;

$spacer: 1.5rem;

// Typography
$font-size-base: 0.875rem;

$h1-font-size: 2.5rem;
$h2-font-size: 2rem;
$h3-font-size: 1.75rem;
$h4-font-size: 1.5rem;
$h5-font-size: 1.25rem;
$h6-font-size: 1rem;

$headings-margin-bottom: $spacer;
$headings-font-weight: 400;
$headings-line-height: 1.1;
$headings-color: $gray-800;

$text-muted: $gray-400;

// Sizes
$sizes: (
  25: 25%,
  50: 50%,
  75: 75%,
  100: 100%,
);

// Border radius

$border-radius: 0.25rem;
$border-radius-lg: 0.3rem;
$border-radius-sm: 0.2rem;

// Shadows

$box-shadow-sm: 0 0.125rem 0.25rem rgba($black, 0.075);
$box-shadow: 0 0.25rem 1rem rgba($black, 0.125);
$box-shadow-lg: 0 0.5rem 3rem rgba($black, 0.15);

// Breadcrumbs

$breadcrumb-padding-x: 0;
$breadcrumb-padding-y: 1rem;
$breadcrumb-bg: transparent;
$breadcrumb-margin-bottom: 0;

// Cards

$card-border-color: $border-color;
$card-spacer-y: 1rem;
$card-spacer-x: $spacer;

// Dropdowns
$dropdown-border-color: $gray-200;

$dropdown-link-color: $body-color;
$dropdown-link-hover-color: $headings-color;

$dropdown-link-active-color: $blue;
$dropdown-link-active-bg: rgba($blue, 0.1);

$dropdown-item-padding-y: 0.375rem;

$dropdown-header-color: inherit;

// Progress bars

$progress-bg: $gray-100;

// Tables

$table-accent-bg: lighten($gray-100, 0.5%);
$table-hover-bg: $gray-100;
$table-head-bg: $gray-100;

// Forms
$input-color: $body-color;
$input-border-color: #dadfe8;

$input-btn-padding-y: 0.5rem;
$input-btn-padding-x: 0.75rem;
$input-btn-line-height: 1.5;

$input-btn-padding-y-sm: 0.125rem;
$input-btn-padding-x-sm: 0.5rem;
$input-btn-line-height-sm: 1.5;

$input-btn-padding-y-lg: 0.625rem;
$input-btn-padding-x-lg: 1rem;
$input-btn-line-height-lg: 1.5;

$input-group-addon-color: $gray-500;

$input-placeholder-color: lighten($gray-400, 4.5%);

$input-group-addon-bg: $gray-100;
$input-group-addon-border-color: $input-border-color;

// Buttons

$btn-secondary-border: $gray-400;
$btn-padding-y: 0.56rem;
$btn-padding-x: 1.375rem;
$btn-font-weight: 500;

$btn-padding-y: $input-btn-padding-y;
$btn-padding-x: $input-btn-padding-x;
$btn-line-height: $input-btn-line-height;

$btn-padding-y-sm: $input-btn-padding-y-sm;
$btn-padding-x-sm: $input-btn-padding-x-sm;
$btn-line-height-sm: $input-btn-line-height-sm;

$btn-padding-y-lg: $input-btn-padding-y-lg;
$btn-padding-x-lg: $input-btn-padding-x-lg;
$btn-line-height-lg: $input-btn-line-height-lg;

// Navbar
$navbar-dark-color: rgba(white, 0.85);
$navbar-dark-hover-color: rgba(white, 1);
$navbar-dark-active-color: rgba(white, 1);
$navbar-dark-disabled-color: rgba(white, 0.25);

// List group
$list-group-border-color: $border-color;
$list-group-border-radius: 0;
$list-group-item-padding-y: 1.125rem;
$list-group-item-padding-x: 1.5rem;

// Modal
$modal-md: 550px;
$modal-sm: 450px;
$modal-backdrop-bg: rgb(29, 37, 49);
$modal-backdrop-opacity: 0.7;
$modal-content-border-width: 0;
$modal-header-border-width: 0;
$modal-inner-padding: $spacer;
$modal-content-border-radius: $border-radius;
$modal-footer-bg: $gray-100;

// Pagination
$pagination-border-color: $input-border-color;
$pagination-hover-border-color: $input-border-color;
$pagination-disabled-border-color: $input-border-color;
$pagination-active-bg: $blue;
$pagination-active-border-color: $blue;

$pagination-line-height: 1.5;

// Navs

/*$nav-tabs-border-color: transparent;
$nav-tabs-border-width: 0;
$nav-tabs-border-radius: 0;
$nav-tabs-link-hover-border-color: transparent;
$nav-tabs-link-active-color: $blue;
$nav-tabs-link-active-border-color: transparent;*/
$nav-tabs-link-hover-border-color: transparent;
$nav-tabs-link-active-bg: white;
$nav-tabs-link-active-border-color: $border-color;
$nav-tabs-link-active-color: $headings-color;

// Popover
$popover-font-size: 0.875rem;
$popover-header-padding-y: 0.25rem;
$popover-header-padding-x: 0.75rem;
$popover-body-padding-y: 0.75rem;
$popover-body-padding-x: 0.75rem;
$popover-border-color: darken($border-color, 10%);
