/* Loader spinner */
#loader {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #f0f3f7;
  z-index: 100000;
  text-align: center;
  line-height: 100vh;
  font-size: 1.25rem;
  font-weight: 400;
}
#loader:after {
  content: " ";
  background-image: url("/img/brand/logo-symbol.svg");
  background-repeat: no-repeat;
  background-position: center;
  animation: pulse 2s ease-in-out infinite;
  height: 192px;
  width: 192px;
  position: absolute;
  top: calc(50% - 76px);
  left: 50%;
  margin-top: -96px;
  margin-left: -96px;
}

@keyframes pulse {
  0% {
    transform: scale(0.5);
  }
  70% {
    transform: scale(0.6);
  }
  100% {
    transform: scale(0.5);
  }
}
.spinner {
  animation: rotate 2s linear infinite;
  z-index: 2;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -15px 0 0 -15px;
  width: 30px;
  height: 30px;
}
.spinner-inline {
  width: 15px;
  height: 15px;
  margin: 0;
  position: initial;
}
.spinner .path {
  stroke: #e9ecef;
  stroke-linecap: round;
  animation: modern-dash 1.5s ease-in-out infinite;
}
.spinner-dark .path {
  stroke: #1f2e3d;
}

@keyframes modern-dash {
  0% {
    stroke-dasharray: 1, 150;
    stroke-dashoffset: 0;
  }
  50% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -35;
  }
  100% {
    stroke-dasharray: 90, 150;
    stroke-dashoffset: -124;
  }
}
