  opacity: 1;
  height: 3.5em;
  width: 1.75em;
  transform-origin: left top;
  border-right: 3px solid #50b652;
  border-top: 3px solid #50b652;
  content: "";
  left: 1.75em;
  top: 3.5em;
  position: absolute;
}

