.registration-page {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  padding: 0 15px;
}

.registration-logo {
  display: block;
  width: $login-logo-width;
  margin: $spacer auto;
}

.registration-box {
  display: block;
  width: 100%;
  max-width: 500px;
  margin: 0 auto 0.75rem;
}
