.kyc-wizard {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 100%;
}

#bas-widget-container {
  min-height: 700px;
  max-width: 500px;
  width: 100%;
}

.account-info {
  border-radius: 26px;
  background-color: $gray-100;
  padding: 0.75rem;
  border: 1px solid $border-color;
}

.icon-callout {
  display: flex;
  margin-bottom: 0.75rem;

  .icon {
    font-size: 2rem;
    background-color: rgba(theme-color('secondary'), 0.1);
    color: theme-color('secondary');
    min-width: 75px;
    height: 75px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
